:root {
  box-sizing: border-box;
  --primary-color: hsl(76, 99%, 40%);
  --secondary-color: black;
}

body {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

.central_logo {
  margin-top: 5em;
  max-width: 90%;
}

.hidden {
  visibility: hidden;
  transform: scale(0.1);
  position: absolute;
  right: 0px;
}

.btn {
  background-color: var(--primary-color);
  border-radius: 4px;
  border: 0;
  box-shadow: rgba(1, 60, 136, 0.5) 0 -1px 3px 0 inset,
    rgba(0, 44, 97, 0.1) 0 3px 6px 0;
  color: var(--secondary-color);
  cursor: pointer;
  font-family: "Space Grotesk", -apple-system, system-ui, "Segoe UI", Roboto,
    Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  font-weight: 700;
  line-height: 24px;
  margin: 0;
  width: fit-content;
  padding: 1em 3em;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  transition: all 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);
}

/* SEARCHBAR */

.searchbar {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  padding: 1em 2em;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.searchInput {
  border-radius: 50vw;
  height: 2.5em;
  width: 50%;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  text-align: center;
}

.header_logo {
  width: 200px;
}

/* RESULTS */

.results-container {
  text-align: center;
}

/* PAGINATION */

.pagination {
  opacity: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.page-text {
  width: fit-content;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
  padding: 0.5em 1em;
  background-color: black;
  color: black;
  border-radius: 10px;
}

.pagination_btn {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  cursor: pointer;
  width: 5em;
  height: 5em;
  border-radius: 50%;
  border: 0;
  box-shadow: rgba(1, 60, 136, 0.5) 0 -1px 3px 0 inset,
    rgba(0, 44, 97, 0.1) 0 3px 6px 0;
  transition: all 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);
}

/* CARD BOOK */

.card_book {
  padding: 0.5em;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  display: flex;
  justify-content: space-between;
  gap: 1em;
  width: 75vw;
  text-align: left;
  margin: 1em auto 1em;
}

.card_book_textcont {
  display: flex;
  gap: 1em;
  flex-direction: column;
  justify-content: space-between;
}

.card_book_title {
  margin: 0;
  font-size: 2em;
}

.card_book_subtitle {
  font-style: italic;
}

.card_book_author {
  font-weight: 700;
}

.card_book_img {
  width: 128px;
  height: 100%;
}

.btn:hover,
.pagination_btn:hover {
  background-color: hsl(76, 99%, 50%);
  transform: translateY(-2px);
}

@media (max-width: 800px) {
  .searchbar {
    flex-direction: column;
    gap: 1em;
  }

  .btn {
    align-self: center;
  }
}

@media (max-width: 500px) {
  .card_book {
    flex-direction: column-reverse;
    align-items: center;
  }
}
